import React, { Component } from 'react';
import CardHeader from "@material-ui/core/CardHeader";
import Avatar from "@material-ui/core/Avatar";
import IconButton from "@material-ui/core/IconButton";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import Typography from "@material-ui/core/Typography";
import Card from '@material-ui/core/Card';

import {
    FacebookShareButton,
    GooglePlusShareButton,
    LinkedinShareButton,
    TwitterShareButton,
    TelegramShareButton,
    WhatsappShareButton,
    PinterestShareButton,
    VKShareButton,
    OKShareButton,
    RedditShareButton,
    TumblrShareButton,
    LivejournalShareButton,
    MailruShareButton,
    ViberShareButton,
    WorkplaceShareButton,
    LineShareButton,
    EmailShareButton,
} from 'react-share';

import {
    FacebookIcon,
    TwitterIcon,
    TelegramIcon,
    WhatsappIcon,
    GooglePlusIcon,
    LinkedinIcon,
    PinterestIcon,
    VKIcon,
    OKIcon,
    RedditIcon,
    TumblrIcon,
    LivejournalIcon,
    MailruIcon,
    ViberIcon,
    WorkplaceIcon,
    LineIcon,
    EmailIcon,
} from 'react-share';

import "../../../../css/AppBody.css";

class Cards extends Component {
    render() {
        return (
            <div>
                <Card >
                    <CardHeader
                        avatar={
                            <Avatar aria-label="Recipe" >
                                {this.props.author[0]}
                            </Avatar>
                        }
                        title={this.props.author}

                    />
                    <CardContent>
                        <Typography component="p">
                            {this.props.contents}
                        </Typography>
                    </CardContent>
                    <CardActions disableActionSpacing>
                        <FacebookShareButton quote="WhatTimeIsItNow?" hashtag="#WhatTimeIsItNow?" url={"https://anoldstory.github.io/WhatTimeIsItNow/board/" + this.props.id} >
                            <IconButton>
                                <FacebookIcon size={32} round={true} />
                            </IconButton>
                        </FacebookShareButton>
                    </CardActions>
                </Card>
            </div>
        );
    }
}

export default Cards;